import React, {FC, useState} from 'react'
import {useHistory} from 'react-router-dom'
import {useSelector, useDispatch} from 'react-redux'
import Cookies from 'js-cookie'

import Menu from '@material-ui/core/Menu'
import MenuItem from '@material-ui/core/MenuItem'
import ExitToAppIcon from '@material-ui/icons/ExitToApp'
import IconButton, {IconButtonProps} from '@material-ui/core/IconButton'

import {UserImgBtn} from '../../atoms/Buttons'
import {logout} from '../../../store/auth'
import {IStore} from '../../../interfaces/Store'
import {IAccountMenu} from './ts'

export const AccountMenu: FC<IAccountMenu> = ({hideDrawer}) => {
  const history = useHistory()
  const dispatch = useDispatch()
  const $auth = useSelector((state: IStore) => state.$authReducer)

  const [anchorEl, setAnchorEl] = useState()

  const goToAuth = () => {
    history.push('/login')
    setAnchorEl(null)
    hideDrawer()
  }
  const goToProfile = () => {
    history.push('/profile')
    handleClose()
  }
  const handleClose = () => {
    setAnchorEl(null)
  }
  const handleLogout = () => {
    Cookies.remove('Authorization', {path: ''})
    dispatch(logout())
    handleClose()
  }
  const handleMenuClick: IconButtonProps['onClick'] = (event) => {
    setAnchorEl(event.currentTarget)
  }

  const {isAuth, profile} = $auth
  const userImg = profile?.image

  return (
    <div className="btnGroup">
      {isAuth ? (
        <div>
          <UserImgBtn onClick={handleMenuClick} userImg={userImg} />
          <Menu
            keepMounted
            id="menu-appbar"
            anchorEl={anchorEl}
            onClose={handleClose}
            open={Boolean(anchorEl)}>
            <MenuItem onClick={goToProfile}>Profile</MenuItem>
            <MenuItem onClick={handleLogout}>Exit</MenuItem>
          </Menu>
        </div>
      ) : (
        <IconButton aria-label="open account" onClick={goToAuth}>
          <ExitToAppIcon />
        </IconButton>
      )}
    </div>
  )
}
