import {Reducer} from 'redux'

import {authTypes} from './types'
import {IAuthStore} from '../../interfaces/Store'

export const initialState: IAuthStore = {
  isAuth: false,
  isFetch: false,
  email: '',
  profile: {
    fullName: ''
  }
}

export const $authReducer: Reducer<IAuthStore> = (state = initialState, action) => {
  console.log(action)
  switch (action.type) {
    case authTypes.start: {
      return {...state, isFetch: true}
    }
    case authTypes.logout: {
      return initialState
    }
    case authTypes.success: {
      return {
        isAuth: true,
        isFetch: false,
        ...action.payload
      }
    }
    case authTypes.failure: {
      return {
        ...state,
        isAuth: false,
        isFetch: false,
        errors: action.payload
      }
    }
    default: {
      return state
    }
  }
}
