import {authTypes} from './types'
import {IErrors, ISignInPayload, ISignUpPayload, IProfile} from '../../interfaces/Store'

/**
 * Simple Actions
 */
export const logout = () => ({type: authTypes.logout})
export const loginSaga = () => ({type: authTypes.login})
export const signInStart = () => ({type: authTypes.start})
export const signInSuccess = (payload: IProfile) => ({type: authTypes.success, payload})
export const signInFailure = (payload: IErrors) => ({type: authTypes.failure, payload})

/**
 * Sagas Actions
 */
export const googleSaga = () => ({type: authTypes.googleSaga})
export const signInSaga = (payload: ISignInPayload) => ({type: authTypes.signInSaga, payload})
export const signUpSaga = (payload: ISignUpPayload) => ({type: authTypes.signUpSaga, payload})
