import React, {FC} from 'react'
import IconButton from '@material-ui/core/IconButton'
import AccountCircle from '@material-ui/icons/AccountCircle'

import {useUserImgBtnS as useStyle} from './themes'
import {IUserImgBtn} from './ts'

export const UserImgBtn: FC<IUserImgBtn> = ({userImg, onClick}) => {
  const classes = useStyle()

  return (
    <IconButton
      onClick={onClick}
      aria-haspopup="true"
      className={classes.root}
      aria-controls="menu-appbar">
      {userImg ? (
        <img src={userImg} className={classes.img} alt="appbar-logo" />
      ) : (
        <AccountCircle className={classes.circle} />
      )}
    </IconButton>
  )
}
